.main-nav__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: @tablet-width) {
  .main-nav__wrapper {
    width: 441px;
    display: flex;
    justify-content: flex-end;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__wrapper {
    width: 480px;
  }
}
